<template>
  <div class="submit">
    <h1><span class="back-home">&#xe624;</span>提交订单</h1>
    <div class="list-group">
      <div class="list">
        <div class="list-item">
          <img src="~img/app/u5576.png" alt="" class="img">
          <div class="item-info">
            <p><span class="name">鱼香肉丝</span>¥ 26</p>
            <p><span class="num">× 1</span><span class="ready">已上菜</span></p>
          </div>
        </div>
        <div class="list-item">
          <img src="~img/app/u5574.png" alt="" class="img">
          <div class="item-info">
            <p><span class="name">鱼香肉丝</span>¥ 26</p>
            <p><span class="num">× 1</span><span class="unready">待上菜</span></p>
          </div>
        </div>
      </div>
    </div>

    <div class="remarks">
      <h2>备注</h2>
      <div class="remark-cnt">
        不要辣 少油 不要葱
      </div>
    </div>

    <div class="sumary">
      <p><span class="name">共计</span>2道</p>
      <p><span class="name">原价</span>¥ 56</p>
      <p><span class="name">商品折扣优惠</span>- ¥7</p>
      <p><span class="name">首单立减优惠</span>- ¥7</p>
      <p><span class="name">共优惠 ¥ 14</span>合计<span class="money"> ¥ 49</span></p>
    </div>

    <div class="confirm">
      <span class="confirm-btn">在线支付</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Submit',
  data () {
    return {
      labels: ['少油', '不吃香菜', '多放辣'],
      remarks: ''
    }
  },
  methods: {
    handleLabelClick (item) {
      if (this.remarks.indexOf(item) > -1) {
        return
      }
      if (this.remarks) {
        this.remarks += (',' + item)
      } else {
        this.remarks += item
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/viewpadding.styl'

  .submit
    width 100%
    // padding 0 2vw
    background #f2f2f2
    position absolute
    left 0
    top 0
    bottom 0
    h1
      line-height 0.6rem
      background #ffcc00
      text-align center
      font-size 0.32rem
      // text-indent -2vw
      .back-home
        padding-left 2vw
        float left
        font-family iconfont
    .list-group
      padding-top 0.4rem
      .list
        .list-item
          height 1.28rem
          background #fff
          margin 0.04rem 2vw
          display flex
          img
            width 1rem
            height 1rem
            margin 0.14rem
          .item-info
            flex 1
            padding 0.14rem
            p
              line-height 0.6rem
              font-size .28rem
              text-align right
              .name,
              .num
                float left
              .num
                font-size 0.28rem
                color #868686
              .ready,
              .unready
                font-size 0.28rem
                line-height 0.32rem
                padding 0 0.1rem
              .ready
                border 1px solid #25a093
                color #25a093
              .unready
                border 1px solid #e85c5a
                color #e85c5a
    .remarks
      margin 2vw 2vw
      background #fff
      h2
        line-height 0.56rem
        margin 0 2vw
        border-bottom 1px solid #f2f2f2
      .remark-cnt
        line-height 0.56rem
        margin 0 2vw
    .sumary
      margin 2vw 2vw
      background #fff
      line-height 0.56rem
      padding 0 2vw
      text-align right
      .name
        float left
      .money
        color #ff0000
        font-weight bold
        font-size 0.32rem
    .confirm
      margin-top 0.4rem
      text-align center
      .confirm-btn
        list-button()
        background #fc0
        color #000
</style>



