<template>
  <div class="submit">
    <h1><span class="back-home">&#xe624;</span>提交订单</h1>
    <div class="list-group">
      <h2>待下单列表</h2>
      <div class="list">
        <div class="list-item">
          <img src="~img/app/u5576.png" alt="" class="img">
          <div class="item-info">
            <p><span class="name">鱼香肉丝</span>¥ 26</p>
            <p><span class="num">× 1</span></p>
          </div>
        </div>
        <div class="list-item">
          <img src="~img/app/u5574.png" alt="" class="img">
          <div class="item-info">
            <p><span class="name">鱼香肉丝</span>¥ 26</p>
            <p><span class="num">× 1</span></p>
          </div>
        </div>
      </div>
      <div class="statis">
        <p><span class="name">菜品数</span>2道</p>
        <p><span class="name">共计</span><span class="money">¥ 49</span></p>
      </div>
    </div>

    <div class="remarks">
      <h2>备注</h2>
      <div class="textarea">
        <textarea placeholder="
  请输入口味、偏好等要求..." v-model="remarks">
        </textarea>
        <p class="num">
          <span class="num1">0</span>/<span class="num2">50</span>
        </p>
      </div>
      <div class="labels">
        <span class="label"
          v-for="(label, index) of labels"
          :key="index"
          @click="handleLabelClick(label)">{{label}}</span>
      </div>
    </div>

    <div class="confirm">
      <span class="confirm-btn">确认下单</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Submit',
  data () {
    return {
      labels: ['少油', '不吃香菜', '多放辣'],
      remarks: ''
    }
  },
  methods: {
    handleLabelClick (item) {
      if (this.remarks.indexOf(item) > -1) {
        return
      }
      if (this.remarks) {
        this.remarks += (',' + item)
      } else {
        this.remarks += item
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/viewpadding.styl'

  .submit
    width 100%
    // padding 0 2vw
    background #f2f2f2
    position absolute
    left 0
    top 0
    bottom 0
    h1
      line-height 0.6rem
      background #ffcc00
      text-align center
      font-size 0.32rem
      // text-indent -2vw
      .back-home
        padding-left 2vw
        float left
        font-family iconfont
    .list-group
      padding-top 0.4rem
      h2
        line-height 0.48rem
        margin-bottom 0.2rem
        margin-left 2vw
      .list
        .list-item
          height 1.28rem
          background #fff
          margin 0.04rem 2vw
          display flex
          img
            width 1rem
            height 1rem
            margin 0.14rem
          .item-info
            flex 1
            padding 0.14rem
            p
              line-height 0.6rem
              font-size .28rem
              text-align right
              .name,
              .num
                float left
              .num
                font-size 0.28rem
                color #868686
      .statis
        margin 0.04rem 2vw
        background #fff
        height 1rem
        padding 0.14rem
        p
          line-height 0.32rem
          margin 0.1rem 0
          text-align right
          font-size 0.24rem
          color #666
          .name
            float left
          .money
            color #f00
            font-size 0.38rem
    .remarks
      padding 0.4rem 0
      h2
        line-height 0.48rem
        margin 0 2vw 0.2rem
        // margin-left 2vw
      .textarea
        // margin 0 2vw
        margin 0 2vw 0.2rem
        height 1.28rem
        position relative
        textarea
          border 1px solid #868686
          display block
          width 99.6%
          height 1.28rem
        textarea::-webkit-input-placeholder
          color #868686
          font-size .28rem
          line-height 0.10rem
        .num
          position absolute
          right 2vw
          bottom .5vw
      .labels
        margin 0 2vw
        display flex
        flex-wrap wrap
        .label
          color #868686
          padding 0 0.2rem
          margin .1rem 0.1rem .1rem 0
          border 1px solid #868686
    .confirm
      text-align center
      .confirm-btn
        list-button()
</style>



