<template>
  <div class="home">
    <desk-id></desk-id>
    <promotions></promotions>
    <has-order></has-order>
    <div class="center_h_v">
        <div id="button" class="order_btn">开始点菜</div>
    </div>
    <div class="catigory_tab">
      <button id="cat_tab1" class="cat_tab">热卖商品</button>
      <button id="cat_tab2" class="cat_tab">今日特价</button>
      <button id="cat_tab3" class="cat_tab">本店主推</button>
    </div>
    <!-- <cai-list></cai-list> -->
    <cart></cart>
  </div>
</template>

<script>
import DeskId from './home/Desk'
import HasOrder from './home/HasOrder'
import Promotions from './home/Promotions'
import CaiList from './home/CaiList'
import Cart from './common/Cart'

export default {
  name: 'Home',
  components: {
    DeskId,
    HasOrder,
    Promotions,
    CaiList,
    Cart
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/viewpadding.styl'

  .center_h_v
    h-v-center()
    padding 0.2rem
    border 1px solid #f2f2f2
    .order_btn
      list-button()
      background #fc0
      color #000
  .catigory_tab
    text-align center
    padding 0.3rem 0
    .cat_tab
      background: #e85c5a;
      color: #fff;
      font-size: .2rem;
      padding: .1rem .2rem;
      border-radius: .2rem;
      border: none;

</style>



