<template>
  <div class="cart-bar">
    <p>
      <span class="cart"><i class="cai_num">2</i></span>
      <span class="t_money_num">¥28</span>
      <span class="confirm_order">下单</span>
    </p>
    <p class="tip">已优惠5元，再买<span>15</span>元可减<span>20</span>元</p>
  </div>
</template>

<script>

export default {
  name: 'SearchInput'
}
</script>

<style lang="stylus" scoped>
  @import '~styles/viewpadding.styl'

  .cart-bar
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: .96rem;
    line-height: .96rem;
    background: #000
    .tip
      height: .42rem;
      line-height: .42rem;
      font-size 0.32rem
      padding: 0 .2rem 0 1.6rem;
      background: #fae2b0;
      position: absolute;
      top: -.43rem;
      left: 0;
      right: 0;
    .cart
      display: inline-block
      width: 1.06rem
      height: 1.06rem
      background url('~img/app/u5799.png') #fc0 .2rem .24rem no-repeat
      background-size: .58rem .58rem
      border-radius: .53rem
      position: absolute
      left: 2vw
      top: -.3rem
      z-index: 2
      .cai_num
        display: inline-block;
        width: .28rem
        height: .28rem
        line-height: .28rem
        background: #f00;
        color: #fff;
        font-style: normal;
        text-align: center;
        border-radius: .14rem
        position: absolute;
        z-index: 3
        top: .1rem
        right: .02rem
    .t_money_num
      color: #fff;
      font-size: .32rem;
      height 0.36rem
      line-height 0.36rem
      font-size 0.36rem
      padding-left 1.6rem
    .confirm_order
      display inline-block
      width 1.66rem
      height 0.96rem
      line-height 0.96rem
      text-align center
      font-size 0.36rem
      color #666666
      background rgb(255, 204, 0)
      position absolute
      right 0
</style>



