<template>
  <div class="has-order">
    <div class="order-info">
      <p>已点菜品：<span class="cai-num">2</span></p>
      <p>订单总额：<span class="money">¥ 28</span></p>
    </div>
    <div class="btn-group">
      <span class="add-btn">加菜</span>
      <span class="pay-btn">结算</span>
    </div>
  </div>
</template>

<script>

export default {
  name: 'HasOrder'
}
</script>

<style lang="stylus" scoped>
  @import '~styles/viewpadding.styl'

  .has-order
    space-between()
    height 0.94rem
    border 1px solid #f2f2f2
    div
      flex 1
      padding 0.2rem
    .order-info
      flex 1
      .money
        color #e85c5a
    .btn-group
      flex 2
      .add-btn,
      .pay-btn
        list-button()
        background #fc0
        color #000
</style>



